<template lang="pug">
.neuro-slider
  input.range(
    type="range",
    :min="properties.min",
    :max="properties.max",
    :step="properties.step",
    :value="properties.value",
    @input="inputHandler($event)"
  )
</template>

<script>
import propertiesHandler from '@/mixins/propertiesHandler'
export default {
  name: 'NeuroInputSlider',
  mixins: [propertiesHandler],
  methods: {
    inputHandler(event) {
      const val = Number(event.target.value)
      const model = this.properties.modelIdent
      this.$emit('input-handler', val, model)
    },
  },
  props: {
    min: {
      type: Number,
      default() {
        return 1
      },
    },
    max: {
      type: Number,
      default() {
        return 16
      },
    },
    value: {
      type: Number,
      default() {
        return 1
      },
    },
    step: {
      type: String,
      default() {
        return '1'
      },
    },
  },
}
</script>
